import React from 'react';
import DateFnsUtils from '@date-io/date-fns';
import { makeStyles, TableBody, TableCell, TableHead, Table, Button } from '@material-ui/core';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';
import Checkbox from '@material-ui/core/Checkbox';
import Toolbar from '@material-ui/core/Toolbar';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import CircularProgress from '@material-ui/core/CircularProgress';
import { ACTIVE_STATUSES, DISABLED_STATUSES, TIKTOK_CAMPAIGN_COLUMNS, TIKTOK_COLUMNS, TIKTOK_GROUP_COLUMNS } from './constants';
import ApiClient from '../../services/apiClient';
import { groupsByCampaign, campaignsByAdvertiser, todayStats, changeCampaignStatus, changeGroupStatus } from '../../constants/endpoints';
import { MetricsItem } from '../../pages/Dashboard/models';
import StatusCircle from '../StatusCircle';

const TikTokList: React.FC<{ show: boolean; columns: any[]; endpoint: string; children?: any, filterable?: boolean, date?: Date, level: string }> = ({
  show, columns, endpoint, children, filterable, date, level,
}) => {
  const classes = useStyles();
  const [statistics, setStatistics] = React.useState([]);
  const [isLoading, setLoading] = React.useState(true);
  const [isFilterActive, setFilterActive] = React.useState(false);
  const [openRows, setOpenRows] = React.useState<string[]>([]);
  const [checkedRows, setCheckedRows] = React.useState<string[]>([]);
  const [removedRows, setRemovedRows] = React.useState<string[]>([]);
  const [updateIndex, setUpdateIndex] = React.useState<number>(0);

  React.useEffect(() => {
    if (!filterable) return;
    const idsFromStorage = JSON.parse(window.localStorage.getItem('filter') || '[]') || [];
    setRemovedRows(idsFromStorage);
    setUpdateIndex(updateIndex + 1);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    if (!show) {
      setOpenRows([]);
    }
  }, [show])

  React.useEffect(() => {
    if (!show) return;
    if (filterable && updateIndex === 0) return;
    const loadItems = async () => {
      try {
        const stats = await ApiClient.GET(endpoint, filterable
          ? { params: { excludeIds: removedRows, date: date ? date.toISOString() : undefined } }
          : { params: { date: date ? date.toISOString() : undefined } });
        setStatistics(stats
          .map(({ metrics, ...item }: MetricsItem) => ({ ...metrics, ...item }))
          .sort((a: any, b: any) => Number(b.impressions) - Number(a.impressions)));
        setLoading(false);
      } catch (error) {
        setLoading(false);
        console.error(error);
      }
    }
    loadItems();
    const interval = setInterval(loadItems, 60 * 1000)
    return () => clearInterval(interval);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [show, updateIndex, filterable, date]);

  const handleRowClick = (id: string) => {
    if (id === 'overall') return;
    if (openRows.indexOf(id) === -1) {
      setOpenRows([...openRows, id]);
      return;
    }
    setOpenRows(openRows.filter(item => item !== id))
  }

  const handleCheck = (id: string) => {
    if (checkedRows.indexOf(id) === -1) {
      setCheckedRows([...checkedRows, id]);
      return;
    }
    setCheckedRows(checkedRows.filter(item => item !== id));
  }

  const handleRemove = async () => {
    const newIds = [...removedRows, ...checkedRows];
    setFilter(newIds);
  };

  const handleRemoveOther = () => {
    const newIds = statistics
      .map((item: any) => item.id)
      .filter((item: any) => checkedRows.indexOf(item) === -1);
    setFilter(newIds);
  };

  const handleReset = () => {
    setFilter([]);
  };

  const setFilter = (ids: string[]) => {
    const newIds = ids.filter(item => item !== 'overall');
    setRemovedRows(newIds);
    setUpdateIndex(updateIndex + 1);
    setOpenRows([]);
    setCheckedRows([]);
    setFilterActive(false);
    window.localStorage.setItem('filter', JSON.stringify(newIds));
  };

  const changeStatus = async (id: string, value: boolean) => {
    const statusEndpoint = level === 'campaign' ? changeCampaignStatus(id) : changeGroupStatus(id);
    await ApiClient.PUT(statusEndpoint, { body: { status: value } });
    setUpdateIndex(updateIndex + 1);
  }

  const renderColumn = (column: any, row: any) => {
    if (column.key === 'checkbox') {
      if (row.id === 'overall') {
        return (<TableCell component='td' />);
      }
      if (filterable && isFilterActive) {
        return (
          <TableCell key={column.key} component='td' onClick={(e) => e.stopPropagation()}>
            <Checkbox
              size={'small'}
              color={'primary'}
              onChange={(e) => handleCheck(row.id)}
              checked={checkedRows.indexOf(row.id) !== -1}
              className={classes.checkbox}
            />
          </TableCell>
        );
      }
      return (
        <TableCell component='td'>
          <StatusCircle value={row.status} />
        </TableCell>
      );
    }
    if (column.key === 'name' && level !== 'advertiser') {
      return (
        <TableCell
          style={{
            ...column.style,
            textAlign: column.left ? 'left' : 'right',
            color: column.color ? column.color(row[column.key]) : null
          }}
          key={column.key}
          component='td'
        >
          <div className={classes.nameWrapper}>
            <div>
              {row[column.key]}
            </div>
            <div onClick={(e: any) => e.stopPropagation()}>
              {ACTIVE_STATUSES.indexOf(row.status) !== -1 && (
                <div className={classes.toggleButton} onClick={() => changeStatus(row.id, false)}>
                  Disable
                </div>
              )}
              {DISABLED_STATUSES.indexOf(row.status) !== -1 && (
                <div className={classes.toggleButton} onClick={() => changeStatus(row.id, true)}>
                  Enable
                </div>
              )}
            </div>
          </div>
        </TableCell>
      )
    }
    return (
      <TableCell
        style={{
          ...column.style,
          textAlign: column.left ? 'left' : 'right',
          color: column.color ? column.color(row[column.key]) : null
        }}
        key={column.key}
        component='td'
      >
        {column.format ? column.format(row[column.key]) : row[column.key]}
      </TableCell>
    );
  }

  if (!show) {
    return null;
  }

  if (isLoading) {
    return (
      <TableRow style={{ backgroundColor: getBackgroundColor(level) }}>
        <TableCell component='td' colSpan={columns.length}>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <CircularProgress />
          </div>
        </TableCell>
      </TableRow>
    )
  }
  return (
    <>
      {statistics.map((row: any) => (
        <React.Fragment key={row.id}>
          <TableRow className={level !== 'group' && row.id !== 'overall' ? classes.clickable : undefined} onClick={() => handleRowClick(row.id)} style={{ backgroundColor: getBackgroundColor(level) }}>
            {columns.map((column) => renderColumn(column, row))}
          </TableRow>
          {children && children(row.id, openRows.indexOf(row.id) !== -1)}
        </React.Fragment>
      ))}
      {filterable && (
        <>
          {!isFilterActive ? (
            <Button onClick={() => setFilterActive(true)} color={'primary'}>
              Activate filter
            </Button>
          ) : (
            <Button onClick={() => setFilterActive(false)} color={'primary'}>
              Disable filter
            </Button>
          )}
          {removedRows.length > 0 && (
            <Button onClick={() => handleReset()} color={'primary'}>
              Reset filter
            </Button>
          )}
          {checkedRows.length > 0 && (
            <Toolbar component={'div'} className={classes.footer}>
              <div>
                <Button color={'primary'} onClick={handleRemoveOther}>Remove Other</Button>
                <Button color={'primary'} onClick={handleRemove}>Remove</Button>
              </div>
            </Toolbar>
          )}
        </>
      )}
    </>
  );
}

const TikTokTable: React.FC<Props> = () => {
  const classes = useStyles();
  const [currentDate, setCurrentDate] = React.useState(new Date());

  return (
    <div className={classes.table}>
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <DatePicker
          value={currentDate}
          onChange={(date: any) => setCurrentDate(date)}
          disableFuture
          inputVariant='outlined'
          InputProps={{ classes: { root: classes.input } }}
        />
      </MuiPickersUtilsProvider>
      <TableContainer classes={{ root: classes.customTableContainer }}>
        <Table stickyHeader>
          <TableHead>
            <TableRow>
              {TIKTOK_COLUMNS.map((column: any) =>
                column.key === 'checkbox' ? (
                  <TableCell key={column.key} component='th' className={classes.checkboxCell} />
                ) : (
                  <TableCell
                    style={{ width: column.width, textAlign: column.left ? 'left' : 'right' }}
                    key={column.key}
                    component='th'
                  >
                    {column.name}
                  </TableCell>
                ))}
            </TableRow>
          </TableHead>
          <TableBody>
            <TikTokList
              endpoint={todayStats}
              columns={TIKTOK_COLUMNS}
              date={currentDate}
              level={'advertiser'}
              show
              filterable
            >
              {(advertiserId: string, isAdvertiserExpanded: boolean) => (
                <TikTokList
                  show={isAdvertiserExpanded}
                  endpoint={campaignsByAdvertiser(advertiserId)}
                  columns={TIKTOK_CAMPAIGN_COLUMNS}
                  date={currentDate}
                  level={'campaign'}
                >
                  {(id: string, isExpanded: boolean) => (
                    <TikTokList
                      endpoint={groupsByCampaign(id)}
                      columns={TIKTOK_GROUP_COLUMNS}
                      show={isExpanded}
                      date={currentDate}
                      level={'group'}
                    />
                  )}
                </TikTokList>
              )}
            </TikTokList>
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  )
}

const useStyles = makeStyles((theme) => ({
  clickable: {
    cursor: 'pointer',
  },
  table: {
    '& td': {
      borderBottom: '1px solid #ccc',
      '& + td': {
        borderLeft: '1px solid #ccc',
      },
      [theme.breakpoints.down('lg')]: {
        fontSize: '1rem',
        padding: 4,
      },
      [theme.breakpoints.down('sm')]: {
        fontSize: '0.6rem',
        padding: 4,
        width: 45,
      },
    },
    '& th': {
      '& + th': {
        borderLeft: '1px solid #ccc',
      },
      [theme.breakpoints.down('lg')]: {
        fontSize: '1rem',
        padding: 4,
      },
      [theme.breakpoints.down('sm')]: {
        fontSize: '0.6rem',
        padding: 4,
      },
    },
  },
  customTableContainer: {
    overflowX: 'initial',
  },
  bold: {
    fontWeight: 'bold',
  },
  row: {
    '&:hover': {
      backgroundColor: '#cccaca',
    },
    backgroundColor: '#ececec',
    cursor: 'pointer',
  },
  footer: {
    width: '100%',
    backgroundColor: '#fff',
    position: 'fixed',
    top: 'auto',
    bottom: 0,
  },
  checkboxCell: {
    width: 30,
  },
  checkbox: {
    margin: -5,
  },
  input: {
    margin: '1rem',
    '& input': {
      padding: '0.5rem',
    },
  },
  nameWrapper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
  },
  toggleButton: {
    cursor: 'pointer',
    fontSize: '0.8rem',
    marginTop: '-0.2rem',
    color: theme.palette.primary.main,
    [theme.breakpoints.down('lg')]: {
      fontSize: '0.7rem',
    },
    [theme.breakpoints.down('sm')]: {
      fontSize: '0.6rem',
    },
  },
}));

const getBackgroundColor = (level: string): string => {
  switch (level) {
    case 'campaign':
      return '#ededed';
    case 'group':
      return '#dfdfdf';
    default:
      return '#fff';
  }
}

interface Props {
}

export default TikTokTable;