import React from 'react';
import { makeStyles } from '@material-ui/core';
import { ACTIVE_STATUSES, DELETED_STATUSES, DISABLED_STATUSES } from '../TikTokTable/constants';

interface Props {
  value: any;
}

const useStyles = makeStyles((theme: string) => ({
  circleWrapper: {
    display: 'flex',
    justifyContent: 'center',
  },
  circle: {
    width: 15,
    height: 15,
    borderRadius: 8,
    margin: 6,
  },
}));


const getStatusColor = (value: string) => {
  if (ACTIVE_STATUSES.indexOf(value) !== -1) {
    return '#13cf10';
  }
  if (DISABLED_STATUSES.indexOf(value) !== -1) {
    return '#ff3939';
  }
  if (DELETED_STATUSES.indexOf(value) !== -1) {
    return '#adabab';
  }
  return '#f9e81f';
}

const StatusCircle: React.FC<Props> = ({ value }) => {
  const classes = useStyles();

  return (
    <div className={classes.circleWrapper}>
      <div className={classes.circle} style={{ backgroundColor: getStatusColor(value) }} />
    </div>
  );
}

export default StatusCircle;
